import React, { FC, useEffect, useRef } from 'react';
import { DOMElement, PolygonProps } from './Polygon.types';
import { draw } from '../../svg/svg.methods';
import { divide, ngon, rad, Cubic } from '../../geometry';
import { FaBeer } from 'react-icons/fa';
import { Svg } from '../Svg/Svg';

export const Polygon: FC<PolygonProps> = ({
	sides = 6,
	rotation = 0,
	scale = 1,
	cornerRadius = 0,
	fillColor = '#000',
	strokeColor = '#000',
	lineWidth = 0.1,
	children = <FaBeer/>
}) => {
	const [points, viewBox] = ngon(sides, scale, rad(rotation), cornerRadius, [0, 0]);
	const scaledLineWidth = lineWidth * scale;
	const hl = scaledLineWidth > 0 ? scaledLineWidth/2:0;
	const newViewBox:Cubic = [viewBox[0]-hl, viewBox[1]-hl, viewBox[2]+scaledLineWidth, viewBox[3]+scaledLineWidth];
	return (<Svg viewBox={newViewBox} width='100px' height='100px'>
		<path d={draw(points)} fill={fillColor} stroke={strokeColor} strokeWidth={scaledLineWidth}/>
		{children}
	</Svg>);
};